.container-info_perso{
    position: absolute;
    top: 35%;
    left: 60px;
}

.container-info_perso.light{
    color: #1E1E1E;
}

.container-info_perso h1{
    font-size: 100px;
}

.container-info_perso h2{
    font-family: Poppins;
    font-size: 30px;
    font-weight: 10;
    margin-bottom: 30px;
}

.container-info_perso a{
    font-family: Poppins;
    font-size: 20px;
    font-weight: 10;
    margin: 0;
    text-decoration: none;
    color: #f1f1f1;
    border: solid #f1f1f1;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    transition: all 0.3s ease;
}

.container-info_perso a:hover{
    color: #1E1E1E;
    background-color: #f1f1f1;
}

.container-info_perso.light a{
    font-family: Poppins;
    font-size: 20px;
    font-weight: 10;
    margin: 0;
    text-decoration: none;
    color: #1E1E1E;
    border: solid #1E1E1E;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    transition: all 0.3s ease;
}

.react-chatbot-kit-chat-header{
    border-radius: 0 !important;
}

.react-chatbot-kit-chat-input{
    border-radius: 0 !important;
}

.container-chatbot-close{
    position: absolute;
    bottom: 30px;
    right: 30px;
    width: 275px;
    height: 500px;
    z-index: 500;
}

.cross{
    z-index: 10000;
    position: absolute;
    top: 5px;
    right: 5px;
    width: 24px;
    height: 24px;
}

.button-chatbot{
    position: absolute;
    bottom: 20px;
    right: 60px;
    width: 100px;
    height: 100px;
    background-color: #f1f1f1;
    border-radius: 50px;
}

.button-chatbot.light{
    background-color: #1E1E1E;
}

.button-chatbot.light::after{
    color: #f1f1f1;
}

.button-chatbot::after{
    content: 'About me';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
}

.react-chatbot-kit-chat-container{
    position: absolute !important;
    bottom: 0px;
    right: 0px;
    border: solid #f1f1f1;
    border-radius: 5px;
    font-family: Poppins;
}

.react-chatbot-kit-chat-bot-message{
    background-color: #1E1E1E !important;
}

.react-chatbot-kit-chat-bot-message-arrow{
    border-right: 8px solid #1E1E1E !important;
}

.react-chatbot-kit-chat-btn-send {
    background-color: #1E1E1E !important;
}

@media screen and (max-width:1024px){

    .container-info_perso h1{
        font-size: 80px;
    }

    .container-chatbot-close{
        position: absolute;
        bottom: 30px;
        right: 30px;
        height: 400px;
    }  

    .container-info_perso h2{
        font-family: Poppins;
        font-size: 30px;
        font-weight: 10;
        margin: 0;
        margin-bottom: 30px;
    }

    .react-chatbot-kit-chat-inner-container{
        height: 400px !important;
    }

    .react-chatbot-kit-chat-message-container{
        height: 324px !important;
    }
}

